.body{
  background-image: url('https://static.vecteezy.com/system/resources/previews/000/676/597/original/abstract-blue-social-media-background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}
.heading{
  text-align: center;
  font-family:'Times New Roman', Times, serif;
  font-size: 350%;
  letter-spacing: 10px;
  color: black;
  text-shadow: 10px 10px 20px black;
}
.container{
  display: inline-block;
  margin-left: 9%;
  margin-top: 20px;
}
.container1{
  display: inline-block;
  height: 300px;
  width: 400px;
  border-radius: 10px;
  background: linear-gradient(45deg, red, white, red);
  box-shadow: 0px 0px 10px gray;
  margin-right: 30px;
  margin-bottom: 30px;
  cursor: pointer;
}
.container2{
  display: inline-block;
  height: 300px;
  width: 400px;
  border-radius: 10px;
  background: linear-gradient(45deg, rgb(255, 255, 0), white, rgb(255, 255, 0));
  box-shadow: 0px 0px 10px gray;
  margin-right: 30px;
  margin-bottom: 30px;
  cursor: pointer;
}
.container3{
  display: inline-block;
  height: 300px;
  width: 400px;
  border-radius: 10px;
  background: linear-gradient(45deg, rgb(255, 0, 115), white, rgb(255, 0, 115));
  box-shadow: 0px 0px 10px gray;
  margin-right: 30px;
  margin-bottom: 30px;
  cursor: pointer;
}
.container4{
  display: inline-block;
  height: 300px;
  width: 400px;
  border-radius: 10px;
  background: linear-gradient(45deg, skyblue, white, skyblue);
  box-shadow: 0px 0px 10px gray;
  margin-right: 30px;
  margin-bottom: 30px;
  cursor: pointer;
}
.container5{
  display: inline-block;
  height: 300px;
  width: 400px;
  border-radius: 10px;
  background: linear-gradient(45deg,rgb(21, 69, 182), white, rgb(21, 69, 182));
  box-shadow: 0px 0px 10px gray;
  margin-right: 30px;
  margin-bottom: 30px;
  cursor: pointer;
}
.container6{
  display: inline-block;
  height: 300px;
  width: 400px;
  border-radius: 10px;
  background: linear-gradient(45deg, green, white, green);
  box-shadow: 0px 0px 10px gray;
  margin-right: 30px;
  margin-bottom: 30px;
  cursor: pointer;
}
.youtube_image{
  height: 300px;
  width: 300px;
  border-radius: 10px;
  margin-left: 50px;
}
.google_image{
  height: 300px;
  width: 300px;
  border-radius: 10px;
  margin-left: 50px;
}
.instagram_image{
  height: 300px;
  width: 500px;
  margin-left: -50px;
}
.twitter_image{
  height: 300px;
  width: 300px;
  cursor: pointer;
  border-radius: 50%;
  margin-left: 50px;
}
.linkedin_image{
  height: 300px;
  width: 300px;
  border-radius: 50%;
  margin-left: 50px;
}
.mail_image{
  height: 300px;
  width: 280px;
  margin-left: 60px;
}

.container1:hover{
  box-shadow: 0px 0px 30px red;
}
.container2:hover{
  box-shadow: 0px 0px 30px yellow;
}
.container3:hover{
  box-shadow: 0px 0px 30px rgb(255, 0, 115);
}
.container4:hover{
  box-shadow: 0px 0px 30px skyblue;
}
.container5:hover{
  box-shadow: 0px 0px 30px rgb(21, 69, 182);
}
.container6:hover{
  box-shadow: 0px 0px 30px green;
}